.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-board {
  display: inline-block;
  flex-wrap: wrap;
  justify-content: center;
  width: 90vh;
  height: 60vh;
  transform: skew(12deg);
  padding-top: 40px;
  margin: 20px auto;
	box-shadow: 2px 20px 21px #000;
}
.tile {
	transition: all 0.5s;
	height: 10%;
	border: 1px solid black;
	width: 10%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0;
	padding: 0;
	position: relative;
}
.tile:hover p {
	box-shadow: 2px 20px 21px #000;
	padding-bottom: 10px;
}
.tile p {
	box-shadow: 2px 20px 21px #000;
	transition: all 0.5s;
	margin: 0;
	padding: 0;
	transform: skew(-12deg);
	font-weight: bold;
	position: absolute;

}
.tile.black {
	background-color: black;
	color: white;
}
.tile.white {
	border: 1px solid black inset;
}
.lives{
	display: inline-flex;
	margin: 0 auto;
	clear: both;
	height: 50px;
}
.lives div {
	width: 50px;
	height: 50px;
	overflow: hidden;
	position: relative;
}
.lives div img{
	position: absolute;
	display: block;
	width: 160px;
	left: -56px;
	top: -43px;
	transition: all 0s;
}
.tile.active{
	background-color: green;
	position:relative;
}
.tile.active:before{
	content: '';
	display: block;
	background-image:url("./dog.png");
	background-position-x: -79px;
	background-position-y: -58px;
	background-size:211px;
	position:absolute;
	height: 60px;
	width: 60px;
	transform: skew(-12deg);
}
.tile.false{
	text-indent: -99999px;
}
